import { auth } from "firebase";

const initState = {
    authError:null,

}

const authReducer = (state = initState,action) =>{
    switch(action.type){
        //  action  reducer 
        case 'LOGIN_ERROR':
            console.log('')
            return {
                ...state,
                authError:''           
            }
        
        case 'LOGIN_SUCCESS':
            console.log('')
            return{
                ...state,
                authError:null
            }

        // 
        case 'SIGNOUT_SUCCESS':
            console.log('');
            return state;
        // 
        case 'SIGNUP_SUCCESS':
            console.log('');
            return{
                ...state,
                authError:null
            }
        
        case 'SIGNUP_ERROR':
            console.log('');
            return{
                ...state,
                authError:action.err.message
                // 
            }


        default:
        return state;

    }
     
    return state;
}

export default authReducer;