//  render 
const initState = {
    // 
    comingUpEventData:''
}

const upComingEventReducer = ( state = initState , action ) =>{
    switch(action.type){
        // case
        case 'CREATE_UPCOMING_EVENT':
            console.log('!!!!',action.concertBasicListArray);
            // action.dataArray
            return{
                ...state,
                comingUpEventData:action.concertBasicListArray
            }
        default:
            return state
    }
    // state
}


export default upComingEventReducer;


