import React from 'react'
import ReactDOM from 'react-dom'
import { HashRouter } from "react-router-dom";
import { createStore, applyMiddleware } from 'redux'
import { composeWithDevTools } from 'redux-devtools-extension'
//reducers
import rootReducer from '../src/store/reducers/rootReducer'
// this is the binding layer redux
import { Provider } from 'react-redux'
import thunk from 'redux-thunk'
import { reduxFirestore, getFirestore } from 'redux-firestore'
import { reactReduxFirebase, getFirebase } from 'react-redux-firebase'
import firebase from './config/firebaseConfig'
// firebasefirebase project


import logger from 'redux-logger';



// 
import App from './App';

// import react thunk middleware
const store = createStore(rootReducer,
    composeWithDevTools(
        applyMiddleware(thunk.withExtraArgument({getFirebase,getFirestore})),
        applyMiddleware(logger),
        reduxFirestore(firebase),
        reactReduxFirebase(firebase,
            {useFirestoreForProfile:true,
                userProfile:'users',
                attachAuthIsReady:true}
            )
        // 
    ));




// inside the function need to put a route reducer.
// reducer

// withExtraArgument action
// return (dispatch, getState, 'another parameter must be an object')  code

//   ready  render 

store.firebaseAuthIsReady.then(() => { // state is ready here
    ReactDOM.render(<Provider store={store}><HashRouter><App /></HashRouter></Provider>, document.getElementById("root"));
})